/* app.css */

html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
}

:root {
  --primary: #0a192f;
  --secondary: #64ffda;
  --text-primary: #ccd6f6;
  --text-secondary: #8892b0;
  --bg-light: #112240;
  --bg-lighter: #233554;
  --white: #e6f1ff;
  --transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: var(--primary);
  color: var(--text-primary);
  line-height: 1.6;
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main.container {
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  width: 100%;
}

section.section {
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary);
}

::-webkit-scrollbar-thumb {
  background: var(--bg-lighter);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--secondary);
}

/* Loading animation styles */
.loading-screen {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--primary);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}

.loader {
  width: 50px;
  height: 50px;
  border: 3px solid var(--bg-lighter);
  border-radius: 50%;
  border-top-color: var(--secondary);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Back to Top button styles */
.back-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: var(--bg-light);
  color: var(--secondary);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  transition: var(--transition);
  z-index: 999;
}

.back-to-top:hover {
  background: var(--bg-lighter);
  transform: translateY(-5px);
}

/* Responsive styles */
@media (max-width: 768px) {
  .section {
    padding: 60px 0;
    min-height: auto;
  }
  
  .container {
    padding: 0 1.5rem;
  }
  
  .back-to-top {
    bottom: 1rem;
    right: 1rem;
    width: 40px;
    height: 40px;
  }
}
